<template>
  <article data-aos="fade-up" 
           data-aos-offset="100"
           data-aos-delay="100"
           data-aos-duration="1000"
           class="persons">
    <div class="persone-background"></div>
    <header>
      <h6>Contact persons</h6>
      <h2>Our contact <span class="yellow">persons</span></h2>
    </header>
    <section class="persons-photo">
      <div class="carousel">
        <ul v-show="windowWidth >641" :style="{ marginLeft: position_photo + 'px'}">
          <li v-for="item in persons" :key="item.name" @touchmove="positionLeft">
            <figure class="info-persone">
              <img class="photo" :src="item.photo" :alt="item.name">
              <figcaption>
                <h4>{{ item.name }}</h4>
                <h5>{{ item.position }}</h5>
                <p><img src="../../assets/image/arroba.png" alt="arroba"> <span>{{ item.email }}</span></p>
              </figcaption>
            </figure>
          </li>
        </ul>
        <swipe ref="mySwipe" v-show="windowWidth <= 641">
          <swipe-item v-for="item in persons" :key="item.name" >
            <figure class="info-persone">
              <img class="photo" :src="item.photo" :alt="item.name">
              <figcaption>
                <h4>{{ item.name }}</h4>
                <h5>{{ item.position }}</h5>
                <p><img src="../../assets/image/arroba.png" alt="arroba"> <span>{{ item.email }}</span></p>
              </figcaption>
            </figure>
          </swipe-item>
        </swipe>
      </div>
      <div class="arrow">
        <button @click="positionLeft">
          <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
              viewBox="0 0 31.494 31.494" style="enable-background:new 0 0 31.494 31.494;" xml:space="preserve">
            <path d="M10.273,5.009c0.444-0.444,1.143-0.444,1.587,0c0.429,0.429,0.429,1.143,0,1.571l-8.047,8.047h26.554
              c0.619,0,1.127,0.492,1.127,1.111c0,0.619-0.508,1.127-1.127,1.127H3.813l8.047,8.032c0.429,0.444,0.429,1.159,0,1.587
              c-0.444,0.444-1.143,0.444-1.587,0l-9.952-9.952c-0.429-0.429-0.429-1.143,0-1.571L10.273,5.009z"/>
          </svg>
        </button>
        <button @click="positionRight">
          <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
              viewBox="0 0 31.49 31.49" style="enable-background:new 0 0 31.49 31.49;" xml:space="preserve">
            <path d="M21.205,5.007c-0.429-0.444-1.143-0.444-1.587,0c-0.429,0.429-0.429,1.143,0,1.571l8.047,8.047H1.111
              C0.492,14.626,0,15.118,0,15.737c0,0.619,0.492,1.127,1.111,1.127h26.554l-8.047,8.032c-0.429,0.444-0.429,1.159,0,1.587
              c0.444,0.444,1.159,0.444,1.587,0l9.952-9.952c0.444-0.429,0.444-1.143,0-1.571L21.205,5.007z"/>
          </svg>
        </button>
      </div>
    </section>
  </article>
</template>

<script>
export default {
  name: 'persons',
  data() {
    return {
      position_photo: 0,
      windowWidth: window.innerWidth,
      swipeOptions: {
        startSlide: 5,
        speed: 300,
        auto: 4000,
        continuous: true,
        disableScroll: false,
        stopPropagation: false
      },
      persons: [
        { name: 'Josef Rau',
          position: 'Chairman of the Board of Directors',
          email: 'j.rau@rau-ag.ch',
          photo: require('../../assets/image/JRau.png')
        },
        { name: 'Daniel Rau',
          position: 'General Manager',
          email: 'd.rau@rau-ag.ch',
          photo: require('../../assets/image/DRau.png')
        },
        { name: 'Herbert Wagner',
          position: 'Head of Production',
          email: 'h.wagner@rau-ag.ch',
          photo: require('../../assets/image/HWagner.png')
        },
        { name: 'Markus Thoma',
          position: 'IT / EDV',
          email: 'm.thoma@rau-ag.ch',
          photo: require('../../assets/image/MThoma.png')
        },
        { name: 'Fabian Wassmer',
          position: 'Process Planning',
          email: 'f.wassmer@rau-ag.ch',
          photo: require('../../assets/image/FWassmer.png')
        },
        { name: 'Josef Rau1',
          position: 'Chairman of the Board of Directors',
          email: 'j.rau@rau-ag.ch',
          photo: require('../../assets/image/JRau.png')
        },
        { name: 'Daniel Rau1',
          position: 'General Manager',
          email: 'd.rau@rau-ag.ch',
          photo: require('../../assets/image/DRau.png')
        },
        { name: 'Herbert Wagner1',
          position: 'Head of Production',
          email: 'h.wagner@rau-ag.ch',
          photo: require('../../assets/image/HWagner.png')
        },
        { name: 'Markus Thoma1',
          position: 'IT / EDV',
          email: 'm.thoma@rau-ag.ch',
          photo: require('../../assets/image/MThoma.png')
        },
        { name: 'Fabian Wassmer1',
          position: 'Process Planning',
          email: 'f.wassmer@rau-ag.ch',
          photo: require('../../assets/image/FWassmer.png')
        }
      ]
    }
  },
  created() {
    window.onresize = (event) => {
      this.windowWidth = window.innerWidth;
    }
  },
  computed: {
    count() {
      let count;
      if(this.windowWidth > 1680) {
        count = 5;
      } else if (this.windowWidth <= 1680 && this.windowWidth > 1280) {
        count = 4;
      } else if (this.windowWidth <= 1280 && this.windowWidth > 1024) {
        count = 3;
      } else if (this.windowWidth <= 1024 && this.windowWidth > 641) {
        count = 2;
      } else if (this.windowWidth <= 641) {
        count = 1;
      }
      return count;
    },
    width() {
      let width = null;
      (this.windowWidth <= 641) ? width = 300 : width = 328;
      console.log(width);
      return width;
    }
  },
  methods: {
    positionLeft() {
      if (this.windowWidth > 641) {
      this.position_photo = Math.min(this.position_photo + this.width * this.count, 0)
      } else if (this.windowWidth <= 641) {
      this.$refs.mySwipe.prev()
      }
    },
    positionRight() {
      if (this.windowWidth > 641) {
      this.position_photo = Math.max(this.position_photo - this.width * this.count, -this.width * (this.persons.length - this.count))
      } else if (this.windowWidth <= 641) {
      this.$refs.mySwipe.next()
      }
    },
  },
  watch: {
    windowWidth: function () {
      if (this.windowWidth <= 641) {
        this.width = 300;
        console.log(this.width);
      }
    }
  }
}
</script>

