<template>
  <ul class="item768" :class="{ showItem: showItem }">
    <h4 class="header-item" @click.stop.prevent="hideItem">
      <button class="arrow-left"></button>
      {{ name_link }}
    </h4>
    <li class="item768-li" v-for="item in items" :key="item.link" :class="{ activeLink: $route.name === item.name }">
      <router-link :to="item.link">{{ item.name }}</router-link>
      <button class="arrow-right" @click.stop.prevent="goLink(item.link)"></button>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'item',
  props: {
    showItem: {
      type: Boolean,
      required: true,
      default: false
    },
    hideItem: {
      type: Function,
      required: true
    },
    name_link: {
      type: String,
      required: true,
      default: ''
    },
    items: {
      type: Array,
      required: true,
      default: []
    }
  },
  methods: {
    goLink (link) {
      this.$router.push(link)
    }
  }
}
</script>

