<template>
  <div class="menu-burger">
    <icon-burger @showMenu="showMenu" @hideMenu="hideMenu" :showBurger="showBurger" :show="show"/>
    <div class="settings-burger" v-show="show">
      <ul>
        <li v-for="item in items" :key="item.link" @click="hideMenu">
          <router-link :to="item.link">
            <figure>
              <img :src="item.icon" :alt="item.name">
              <figcaption>{{ item.name }}</figcaption>
            </figure></router-link>
        </li>
      </ul>
      <ul>
        <li @click="hideMenu"><img src="../../assets/image/eng.png" alt="eng"></li>
        <li @click="hideMenu"><img src="../../assets/image/deu.png" alt="deu"></li>
      </ul>
    </div>
  </div>
</template>

<script>

import IconBurger from './breakpoints768/IconBurger'

export default {
  name: 'menu-burger',
  data() {
    return {
      show: false,
      showBurger: true,
      items: [
        { link: "/contact", name: "Contact", icon: require('../../assets/image/020-telephone-3.png') },
        { link: "/location", name: "Location", icon: require('../../assets/image/location.png') },
        { link: "/sitemap", name: "Sitemap", icon: require('../../assets/image/sitemap.png') }
      ]
    }
  },
  components: { IconBurger },
  methods: {
    showMenu() {
      this.show = true;
      this.showBurger = false;
    },
    hideMenu() {
      this.show = false;
      this.showBurger = true;
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/style/MenuBurger1280.scss';
