export const anzeigen = {
  data() {
    return {
      windowWidth: window.innerWidth,
      mehr_anzeigen: ''
    }
  },
  created() {
    (this.windowWidth > 768) ? this.mehr_anzeigen = true : this.mehr_anzeigen = false;
  },
  mounted() {
    window.onresize = (event) => {
      this.windowWidth = window.innerWidth;
    }
  },
  methods: {
    mehrAnzeigen() {
      this.mehr_anzeigen = true;
    },
    wenigerAnzeigen() {
      this.mehr_anzeigen = '';
    }
  },
  watch: {
    windowWidth: function () {
      if(this.windowWidth > 768) {
        this.mehr_anzeigen = true;
      } else if (this.windowWidth < 769) {
        this.mehr_anzeigen = false;
      }
    }
  }
}